import styled from 'styled-components';
import { Modal } from 'react-bootstrap';
import { H6 } from '../common/Typography';
import { useWalletConnect } from '../../hooks/WalletConnect';
import parse from 'html-react-parser';

const StyledModal = styled(Modal)`
  .modal-dialog {
    max-width: 430px;
    margin: auto;
    @media screen and (max-width: 425px) {
      max-width: 254px;
    }
  }

  .connect-success-content {
    background: #fff;
    border-radius: 10px;
    width: 100%;
    overflow: hidden;
    border: none;
  }

  .modal-header {
    height: 63px;
    background: #e2ae39;
    @media screen and (max-width: 425px) {
      height: 40px;
    }
  }

  .modal-text {
    color: #000;
    margin-bottom: 32px;
    font-weight: 500;
  }
`;

const ModalText = styled(H6)`
  @media screen and (max-width: 489px) {
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 19px !important;
  }
`;

const ContentWrapper = styled.div`
  background: white;
  display: flex;
  align-items: center;
  flex-flow: column;

  .wallet-logo {
    height: 95px;
    width: auto;
    margin-bottom: 40px;
    @media screen and (max-width: 425px) {
      margin-bottom: 7px;
      height: 65px;
    }
  }
`;

const CloseButton = styled.button`
  border: none;
  background: #e2ae39;
  color: #000;
  padding: 8px 40px;
  border-radius: 10px;
  font-weight: 700;
  font-size: 24px;
  @media screen and (max-width: 425px) {
    font-size: 14px;
  }
`;

interface AlertModalProps {
  show: boolean;
  onClose: () => void;
  message: string;
}

const AlertModal = ({ show, onClose, message }: AlertModalProps) => {
  const { activeWallet } = useWalletConnect();

  return (
    <StyledModal show={show} onHide={onClose} centered contentClassName="connect-success-content">
      <Modal.Header />
      <Modal.Body>
        <ContentWrapper>
          <img className="wallet-logo" src={'/favicon.svg'} />
          <ModalText className="modal-text" textAlign="center">
            {parse(message)}
          </ModalText>
          <CloseButton onClick={onClose}>Close</CloseButton>
        </ContentWrapper>
      </Modal.Body>
    </StyledModal>
  );
};

export default AlertModal;
