import { Modal } from 'react-bootstrap';
import styled from 'styled-components';
import { useWalletConnect } from '../../hooks/WalletConnect';
import { H5, P } from '../common/Typography';
import { useMedia } from 'react-use';

const StyledModal = styled(Modal)`
  .modal-dialog {
    max-width: 429px;
    margin: auto;
    @media screen and (max-width: 425px) {
      max-width: 268px;
    }
  }

  .connect-modal-content {
    background: #e2ae39;
    border-radius: 10px;
    padding: 13px;
  }

  .modal-header {
    border-bottom: 0;
    padding: 0;
    margin-bottom: 8px;
  }

  .modal-body {
    padding: 0 46px;
    @media screen and (max-width: 425px) {
      padding: 0 17px;
    }
  }

  .close-btn {
    margin: 0;
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.14);
    color: #ffffff;
    border-radius: 50%;
    width: 31px;
    height: 31px;
    padding: 0;
    border: 0;
    img {
      width: 100%;
    }
  }
`;

const Button = styled.div`
  border: none;
  color: #ffffff;
  background: #000000;
  border-radius: 10px;
  width: 243px;
  margin: auto;
  font-size: 30px;
  font-weight: 700;
  display: flex;
  align-items: center;
  margin-bottom: 26px;
  padding: 13px 22px;
  cursor: pointer;

  img {
    width: 40px;
    height: auto;
    margin-right: 15px;
  }
  @media screen and (max-width: 425px) {
    width: 170px;
    height: 52px;
    font-size: 18px;
    padding: 8px 20px;
    margin-bottom: 11px;
    img {
      width: 30px;
      margin-right: 9px;
    }
  }
`;

const ModalTitle = styled(H5)`
  margin-bottom: 42px;
  @media screen and (max-width: 489px) {
    font-size: 24px;
    margin-bottom: 18px;
  }
`;

const ModalBottomText = styled(P)`
  margin-top: -4px;
  margin-bottom: 15px;
  line-height: 19px;
  @media screen and (max-width: 425px) {
    margin-top: 7px;
    margin-bottom: 0;
    font-size: 10px;
  }
`;

interface ConnectWalletModalProps {
  show: boolean;
  onClose: (connected?: boolean) => void;
}

const ConnectWalletModal = ({ show, onClose }: ConnectWalletModalProps) => {
  const isMobile = useMedia('(max-width: 425px)');
  const { enableWallet } = useWalletConnect();
  const handleConnectWallet = (walletName: string) => async () => {
    try {
      await enableWallet(walletName);
      onClose(true);
    } catch (err) {
      onClose(false);
    }
  };

  return (
    <StyledModal show={show} onHide={onClose} centered contentClassName="connect-modal-content">
      <Modal.Header>
        <button className="close-btn" onClick={() => onClose()}>
          <img src="/assets/images/closeBtn.svg" alt="close-btn" />
        </button>
      </Modal.Header>
      <Modal.Body>
        <ModalTitle textAlign="center" fontWeight={700} color="#000">
          CONNECT YOUR WALLET
        </ModalTitle>
        <div className="d-flex flex-column">
          {isMobile ? (
            ''
          ) : (
            <Button onClick={handleConnectWallet('nami')}>
              <img src="/assets/images/wallets/nami.png" alt="Nami" />
              NAMI
            </Button>
          )}
          <Button onClick={handleConnectWallet('eternl')}>
            <img src="/assets/images/wallets/eternl.png" alt="Eternl" />
            ETERNL
          </Button>
          {isMobile ? (
            ''
          ) : (
            <Button onClick={handleConnectWallet('flint')}>
              <img src="/assets/images/wallets/flint.png" alt="Flint" />
              FLINT
            </Button>
          )}{' '}
          <Button onClick={handleConnectWallet('yoroi')}>
            <img src="/assets/images/wallets/yoroi.png" alt="Yoroi" />
            YOROI
          </Button>
        </div>
        <ModalBottomText color="#ffffff" fontSize="16px" className="text-center" fontWeight={700}>
          By connecting your wallet, you agree to the Terms and Privacy Policy of this website
        </ModalBottomText>
      </Modal.Body>
    </StyledModal>
  );
};

export default ConnectWalletModal;
