import React, { Suspense, lazy } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import ErrorBoundary from './components/ErrorBoundary';
import MainLayout from './components/layouts/main';
import AppLoader from './components/Loader';
import 'bootstrap/dist/css/bootstrap.min.css';

const HomePage = lazy(() => import('./pages/Home'));
const AboutPage = lazy(() => import('./pages/About'));
const AccumulatePage = lazy(() => import('./pages/Accumulate'));
const CollectRewardPage = lazy(() => import('./pages/CollectReward'));
const GovernancePage = lazy(() => import('./pages/Governance'));
const TermsPage = lazy(() => import('./pages/Terms'));
const PrivacyPage = lazy(() => import('./pages/Privacy'));
const ProposalPage = lazy(() => import('./pages/Proposals'));
const MyWalletPage = lazy(() => import('./pages/MyWallet'));
const RedeemPage = lazy(() => import('./pages/Redeem'));

function App() {
  return (
    <ErrorBoundary>
      <BrowserRouter>
        <Suspense fallback={<AppLoader />}>
          <Routes>
            <Route path="/" element={<MainLayout />}>
              <Route index element={<HomePage />} />
              <Route path="/about" element={<AboutPage />} />
              <Route path="/accumulate" element={<AccumulatePage />} />
              <Route path="/collect-rewards" element={<CollectRewardPage />} />
              <Route path="/redeem" element={<RedeemPage />} />
              <Route path="/governance" element={<GovernancePage />} />
              <Route path="/proposals" element={<ProposalPage />} />
              <Route path="/my-wallet" element={<MyWalletPage />} />
              <Route path="/terms" element={<TermsPage />} />
              <Route path="/privacy" element={<PrivacyPage />} />
            </Route>
          </Routes>
        </Suspense>
      </BrowserRouter>
    </ErrorBoundary>
  );
}

export default App;
