import { useState, useEffect } from 'react';
import { useMedia } from 'react-use';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { AiOutlineMenu, AiOutlineClose } from 'react-icons/ai';
import ConnectWalletModal from '../Modals/ConnectWalletModal';
import AlertModal from '../Modals/AlertModal';
import { useWalletConnect } from '../../hooks/WalletConnect';
import {
  NavWrapper,
  ConnectButton,
  Container,
  NavbarCollapse,
  BrandImage,
  MyWalletLink,
  LogoutButton
} from './navigation.styled';

const Navigation = () => {
  const isMobile = useMedia('(max-width: 991px)');
  const navigate = useNavigate();
  const location = useLocation();
  const { myWalletAddress, disableWalletAddress } = useWalletConnect();
  const [openConnectModal, setOpenConnectModal] = useState(false);
  const [menuOpened, setMenuOpened] = useState(false);
  const [openSuccessModal, setOpenSuccessModal] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');

  const handleCloseModal = (connected?: boolean) => {
    setOpenConnectModal(false);
    if (connected !== undefined) {
      setAlertMessage(
        connected ? 'Your wallet is now connected' : 'Your wallet connection was unsuccessful'
      );
      setOpenSuccessModal(true);
    }
  };

  const handleOpenModal = () => {
    if (myWalletAddress) {
      navigate('/my-wallet');
    } else {
      setOpenConnectModal(true);
    }
  };

  useEffect(() => {
    if (isMobile) {
      setMenuOpened(false);
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.display = 'none';
      if (!document.querySelector('.navbar-toggler')?.classList.contains('collapsed')) {
        document.querySelector('.navbar-toggler')?.classList.add('collapsed');
      }
    }
  }, [location.pathname]);

  useEffect(() => {
    if (menuOpened) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.display = 'flex';
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.justifyContent = 'space-between';
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.alignItems = 'start';
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.flexDirection = 'column';
    } else {
      // eslint-disable-next-line @typescript-eslint/ban-ts-comment
      // @ts-ignore
      document.querySelector('#basic-navbar-nav').style.display = 'none';
    }
  }, [menuOpened]);

  const handleLogout = () => {
    // eslint-disable-next-line no-constant-condition
    if (location.pathname == '/my-wallet') navigate('/about');
    disableWalletAddress();
    setOpenSuccessModal(true);
    setAlertMessage('Your wallet is disconnected!');
  };

  useEffect(() => {
    if (menuOpened) {
      (document.querySelector('.page') as HTMLBaseElement).style.display = 'none';
    } else {
      (document.querySelector('.page') as HTMLBaseElement).style.display = 'block';
    }
  }, [menuOpened, navigate]);

  return (
    <NavWrapper expand="lg">
      <Container>
        <Link className="navbar-brand" to="/">
          <BrandImage src="/logo.svg" alt="Md Token Logo" height="84px" />
        </Link>
        <Navbar.Toggle
          aria-controls="basic-navbar-nav"
          onClick={() => setMenuOpened((value) => !value)}
        >
          {menuOpened ? (
            <AiOutlineClose size={36} color="#ffffff" />
          ) : (
            <AiOutlineMenu size={36} color="#ffffff" />
          )}
        </Navbar.Toggle>
        <NavbarCollapse id="basic-navbar-nav">
          <Nav className="ms-auto d-flex align-items-center gap-4">
            <Link to="/about">About</Link>
            <NavDropdown title="STAKING" id="nav-dropdown">
              <NavDropdown.Item>
                <Link to="/accumulate">Accumulate</Link>
              </NavDropdown.Item>
              <NavDropdown.Item>
                <Link to="/collect-rewards">Collect Rewards</Link>
              </NavDropdown.Item>
            </NavDropdown>
            <Link to="/redeem">Redeem</Link>
            <Link to="/governance">Governance</Link>
            <Link to="/proposals">Proposals</Link>
            {myWalletAddress ? (
              <>
                <MyWalletLink to="/my-wallet">MY WALLET</MyWalletLink>
                <LogoutButton onClick={handleLogout}>LOGOUT</LogoutButton>
              </>
            ) : (
              <ConnectButton onClick={handleOpenModal}>CONNECT YOUR WALLET</ConnectButton>
            )}
          </Nav>
          {isMobile ? (
            <Nav className="flex-row terms-privacy">
              <Link to="/terms" className="me-2">
                TERMS
              </Link>
              {' & '}
              <Link to="/privacy" className="ms-2">
                PRIVACY
              </Link>
            </Nav>
          ) : (
            ''
          )}
        </NavbarCollapse>
      </Container>
      {openConnectModal && (
        <ConnectWalletModal show={openConnectModal} onClose={handleCloseModal} />
      )}
      {openSuccessModal && (
        <AlertModal
          show={openSuccessModal}
          onClose={() => setOpenSuccessModal(false)}
          message={alertMessage}
        />
      )}
    </NavWrapper>
  );
};

export default Navigation;
