import styled from 'styled-components';
import { Navbar } from 'react-bootstrap';
import { Link } from 'react-router-dom';

export const NavWrapper = styled(Navbar)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  color: #fff;
  padding: 40px 0;
  .show {
    & > .nav-link {
      color: #e2ae39;
    }
  }
  .nav-link {
    &.active,
    &:focus,
    &:hover {
      color: #e2ae39;
    }
  }
  #nav-dropdown {
    padding: 0;
    &:hover {
      color: #e2ae39;
    }
    &::after {
      border-top-color: #e2ae39;
    }
  }
  .dropdown-menu {
    &.show {
      background-color: #000;
      border: 1px solid #fff;
    }
    a {
      &:hover {
        background-color: transparent;
        color: #e2ae39;
      }
    }
  }
  @media screen and (max-width: 991px) {
    .navbar-nav {
      padding: 0 54px;
      align-items: start !important;
      margin-left: 0 !important;
      gap: inherit !important;
      a {
        margin-bottom: 24px;
        font-size: 24px;
        font-weight: 700;
      }
    }
    .dropdown-menu {
      &.show {
        border: 1px solid transparent;
      }
    }
    #basic-navbar-nav {
      &.show {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: start;
      }
    }
    .terms-privacy {
      font-size: 24px;
      font-weight: 700;
    }
  }

  a {
    color: #fff;
    text-transform: uppercase;
    font-size: 21px;
    font-weight: bold;
    white-space: nowrap;
    @media (max-width: 1440px) and (min-width: 1100px) {
      font-size: 17px;
    }
    @media (max-width: 1100px) and (min-width: 992px) {
      font-size: 15px;
    }
  }
`;

export const ConnectButton = styled.button`
  background: #8f8f8f;
  border: 3px solid #e2ae39;
  border-radius: 32.5px;
  font-size: 21px;
  padding: 0 20px;
  color: white;
  font-weight: bold;
  line-height: 65px;
  white-space: nowrap;
  @media (max-width: 1440px) and (min-width: 1100px) {
    line-height: 46px;
    font-size: 17px;
  }
  @media (max-width: 1100px) and (min-width: 992px) {
    font-size: 15px;
    line-height: 46px;
  }
  @media screen and (max-width: 991px) {
    line-height: 54px;
  }
`;

export const Container = styled.div`
  width: 100%;
  padding: 0 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media (max-width: 1440px) and (min-width: 992px) {
    padding: 0 15px;
  }

  @media screen and (max-width: 991px) {
    padding: 0;
  }
`;

export const NavbarCollapse = styled(Navbar.Collapse)`
  @media (max-width: 1440px) and (min-width: 1024px) {
    .navbar-nav {
      gap: 1rem !important;
    }
  }
  @media (max-width: 1024px) and (min-width: 991px) {
    .navbar-nav {
      gap: 0.7rem !important;
    }
  }
  @media screen and (max-width: 991px) {
    position: fixed;
    top: 150px;
    left: 0;
    bottom: 0;
    width: 100vw;
    background: #000000;
  }
`;

export const BrandImage = styled.img`
  height: 84px;

  @media (max-width: 1440px) and (min-width: 992px) {
    height: 67px;
  }
  @media screen and (max-width: 991px) {
    height: 50px;
    padding-left: 22px;
  }
`;

export const MyWalletLink = styled(Link)`
  background: #e2ae39;
  color: #000;
  display: block;
  border-radius: 32.5px;
  font-size: 21px;
  padding: 0 20px;
  color: white;
  font-weight: bold;
  line-height: 65px;
  white-space: nowrap;
  &:hover {
    color: #ffffff;
  }
  @media (max-width: 1440px) and (min-width: 991px) {
    line-height: 46px;
  }
  @media screen and (max-width: 991px) {
    line-height: 54px;
  }
`;

export const LogoutButton = styled.button`
  background: #e2ae39;
  color: #000;
  display: block;
  border-radius: 32.5px;
  font-size: 21px;
  padding: 0 20px;
  color: white;
  border: 0;
  font-weight: bold;
  line-height: 65px;
  white-space: nowrap;
  &:hover {
    color: #ffffff;
  }
  @media (max-width: 1440px) and (min-width: 991px) {
    line-height: 46px;
  }
  @media screen and (max-width: 991px) {
    line-height: 54px;
    width: 167px;
  }
`;
